import * as React from 'react';
import * as Costants from '../../utils/constants.js';
import AlbumList from '../album/albumList.js';
import UserCombobox from '../input/userCombobox.js';
import Grid from '@mui/material/Grid';
import SearchIcon from '@mui/icons-material/Search';
import Box from '@mui/material/Box'; 
 

export default function Main({ albums, users, searchText, handleChangeSearchText, selectedUser, handleChangeSelectedUser }) {

    return (
        <div className="mainViewContainer">

            {/* Search bar and select user controls */}
            <Grid container spacing={2}>
                <Grid item xs={9.5}>
                    <Box sx={{
                        my: 1, display: 'flex', alignItems: 'center', width: '100%',
                        border: (theme) => `1px solid rgba(0, 0, 0, 0.3)`,
                        borderRadius: 1, bgcolor: 'background.paper'
                    }} >
                        <Costants.Search >
                            <Costants.SearchIconWrapper>
                                <SearchIcon />
                            </Costants.SearchIconWrapper>
                            <Costants.StyledInputBase
                                value={searchText}
                                onChange={handleChangeSearchText}
                                placeholder="Search album" position="right"
                                inputProps={{ 'aria-label': 'search' }} />
                        </Costants.Search>
                    </Box>
                </Grid>
                <Grid item xs={2.5} >
                    <UserCombobox selectedUser={selectedUser} handleChangeSelectedUser={handleChangeSelectedUser} users={users}/> 
                </Grid>
            </Grid>


            {/* Album list */}
            <AlbumList albums={albums} searchText={searchText}/>  
        
        </div>
    );
}