import { useState } from "react";
import { useEffect } from 'react'; 
import axios from "axios";


export function useAllAlbums() {

    //State vars
    const [allAlbums, setAllAlbums] = useState([]);
  
    const URL_ALBUMS = process.env.REACT_APP_API_URL_ALBUMS;
    console.log(URL_ALBUMS);
    console.log(String(URL_ALBUMS));

    //Runs once
    useEffect(() => {

        var configGetAlbums = {
            method: 'get',
            url: String(URL_ALBUMS)
        };


        axios(configGetAlbums)
            .then(function (response) {
                setAllAlbums(response.data.message);
            })
            .catch(function (error) {
                console.log(error);
            });

    }, []);

    return allAlbums;
} 